<template>
  <div class="textareaZone">
    <div class="label">
      <label :for="inputId">{{ labelTitle }}</label>
    </div>
    <div class="inputZone">
      <div
        class="contentZoneItem1"
        :style="{ height: selfHeight, width: selfWidth }"
      >
        <textarea
          :disabled="disableMark"
          :id="inputId"
          :placeholder="placeholder"
          v-model="message"
        ></textarea>
      </div>
      <div class="contentZoneItem2">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import { debounce, processStringAsArray } from "../../../../../common/utils";
export default {
  name: "e-TextareaZone",
  created() {
    const emitTrue = () => {
      this.$emit("setLegitimacyTrue");
    };
    this.debounceEmitTrue = debounce(emitTrue, 500);

    const emitFalse = () => {
      this.$emit("setLegitimacyFalse");
    };
    this.debounceEmitFalse = debounce(emitFalse, 500);

    function emitUpdateTrue() {
      this.$emit("setUpdateStatusTrue");
    }
    this.debounceEmitUpdateTrue = debounce(emitUpdateTrue, 500);

    function emitUpdateFalse() {
      this.$emit("setUpdateStatusFalse");
    }
    this.debounceEmitUpdateFalse = debounce(emitUpdateFalse, 500);
  },
  props: {
    labelTitle: {
      type: String,
      default: "",
    },
    inputId: {
      type: String,
      default: "",
    },
    placeholder: {
      type: String,
      default: "",
    },
    selfHeight: {
      type: String,
      default: "160px",
    },
    selfWidth: {
      type: String,
      default: "55%",
    },

    targetDataGenerateType: {
      type: Number,
      default: 2,
    },
  },
  data() {
    return {
      message: "",
      disableMark: false,
      debounceEmitTrue: null,
      debounceEmitFalse: null,

      originalMessage: "",
      componentStatusCode: "100",

      debounceEmitUpdateTrue: null,
      debounceEmitUpdateFalse: null,
      targetData: null,
    };
  },
  watch: {
    message(newVal) {
      console.log("text are azone ne w  val:", newVal);
      if (newVal !== "") {
        this.debounceEmitTrue();
      } else {
        this.debounceEmitFalse();
      }

      if (newVal !== "") {
        this.$emit("setNoEmptyTrue");
      } else {
        this.$emit("setNoEmptyFalse");
      }

      const processedNewVal1 = processStringAsArray(newVal);
      const processedNewVal2 = processedNewVal1.join("\n");
      if (processedNewVal2 !== this.originalMessage) {
        this.debounceEmitUpdateTrue();
      } else {
        this.debounceEmitUpdateFalse();
      }

      if (this.targetDataGenerateType === 1) {
        if (newVal !== "") {
          const processedNewVal1 = processStringAsArray(newVal);
          const processedNewVal2 = processedNewVal1.join("\n");
          const processedNewVal3 = processedNewVal2.replace(/\n/g, "。");
          this.targetData = processedNewVal3;
        } else {
          this.targetData = null;
        }
      }
      // targetDataGenerateType === 2(default)
      else {
        if (newVal !== "") {
          this.targetData = processStringAsArray(newVal);
          //console.log(this.targetData)
        } else {
          this.targetData = null;
        }
      }

      this.$bus.$emit("updateDataInCookie");
    },
  },
};
</script>

