  name: 'e-Settings',
  beforeRouteEnter(to, from, next) {
    // console.log(to);
    if (window.localStorage.getItem('isProvider') === 'true') {
      window.localStorage.setItem('currentIndex010006', 0)
      next()
    } else {
      next({
        path: '/tools/login'
      })
      window.localStorage.removeItem('currentIndex010006')
      window.localStorage.removeItem('userID')
      window.localStorage.removeItem('username')
      window.localStorage.removeItem('authToken')
      window.localStorage.removeItem('isStaff')
      window.localStorage.removeItem('isSuperUser')
      window.localStorage.removeItem('isProvider')
      window.localStorage.removeItem('providerID')
    }
  },
  beforeCreate() {
    if (window.localStorage.getItem('isProvider') === false) {
      this.$router.replace('/tools/login')
    }
  },
  created() {
    if (window.localStorage.getItem('firstTimeLogin') == 1) {
      const str = '.details'
      //const response2 = await $getProductByID({id: 1});
      //console.log(response2);
      this.isLoading = false
      const clientWidth = document.body.clientWidth
      const clientHeight = document.body.clientHeight
      let targetLeft = (clientWidth - 500) / 2
      let targetTop = (clientHeight - 500) / 2
      $(str).css('left', targetLeft)
      $(str).css('top', targetTop)
      this.viewBoardVisible = true
      // $(str).animate({ left: targetLeft, top: targetTop }, 100);
      this.$bus.$on('infoConfirm', () => {
        // console.log(1);
        this.viewBoardVisible = false
      })
      window.localStorage.setItem('firstTimeLogin', 0)
    }

    // console.log(getCurrentPages());
    this.$nextTick(() => {
      this.timeoutLoadingToWait = () => {
        return new Promise(resolve => {
          setTimeout(() => {
            this.isLoading = false
            resolve()
          }, 1000)
        })
      }

      for (const val of this.compList) {
        if (this.$refs[val.referenceName]) {
          this.$refs[val.referenceName].$data.componentStatusCode = '101'
        }
      }

      //获取settings数据来初始化基本信息页的表单
    })
  },
