  async mounted() {
    // console.log(window.localStorage);
    // this.$toast.error("test", 1000);

    let userinfo = this.$store.state.providerUser.userInfo
    this.userinfo = JSON.parse(window.localStorage.getItem('userinfo'))

    console.log('settings get user info from state: ', userinfo)
    console.log('settings get user info fromlocalstorage: ', this.userinfo)

    let provider_id = Number(window.localStorage.getItem('providerID'))
    console.log('provider id: ', provider_id)
    let response_temp = await request({
      method: 'POST',
      url: '/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/provider/insurance/info/get',
      data: {
        provider_id: provider_id
      },
      auth: {
        username:
          'NginxAuthUsername16381efa069a90cbde99854c81183036a4bd2e4776eb770fa69740f1c5c06aa4',
        password:
          'NginxAuthPassworde0588e3b3c5b240fe3f5fb653d2990f34b52ee0b595acf6553a924b0716cb366'
      },
      headers: {
        'Content-Type': 'application/json'
      }
    })
    console.log(' provider/insurance/info/get  response: ', response_temp)
    if (response_temp.Code == 200) {
      console.log('基本信息请求  200 ， 载入已通过的信息')
      if (response_temp.details.applicant_type == 0)
        response_temp.details.applicant_type = ''
      if (response_temp.details.identify_type == 0)
        response_temp.details.identify_type = ''
      let authdate = {
        first: response_temp.details.first_authentication_time,
        last: response_temp.details.last_authentication_time
      }
      // console.log(authdate)
      this.$store.commit('providerUser/setAuthDate', authdate)

      console.log('commit success')
      console.log(this.$store.commit)
      this.isRegistered = true
      let info = { ...response_temp.details }
      console.log(info)

      for (let i in info) {
        // console.log('for in : ',i,typeof info[i])
        if (typeof info[i] == 'string') {
          // console.log(i,": ",info[i],typeof info[i])
          if (i == 'business_duration_end') {
            if (info[i].slice(0, 10) == '2200-01-01') {
              console.log('是长期')
              this.switchLongTerm()
              this.longTermSwitchable = false
              console.log(this.$data)
              continue
            } else {
              this.$refs[i].value = info[i].slice(0, 10)
              this.$refs[i].disabled = true
              continue
            }
          }
          if (i == 'business_duration_start') {
            this.$refs[i].value = info[i].slice(0, 10)
            this.$refs[i].disabled = true
            continue
          }
          if (i == 'date_created') {
            continue
          }
          if (i == 'introduction') {
            this.$refs[i].message = info[i]
            this.$refs[i].disableMark = true
            continue
          }

          if (i == 'provider_registration_area') {
            console.log(i, 'provider_registration_area entered')
            this.$refs[i].defaultRegion = info[i]
            this.$refs[i].disableMark = true
            continue
          }
          // console.log(i,": ",info[i],typeof info[i])
          if (this.$refs[i]) {
            this.$refs[i].inputText = info[i]
            this.$refs[i].disableMark = true
            // console.log(i,' set! ')
          } else {
            console.log(i, ' dosenot exist!')
          }
        } else {
          if (i == 'provider' || i == 'provider_id') {
            continue
          }
          console.log(this.$refs)
          console.log(this.$store.state)
          if (i == 'identify_type') {
            setTimeout(() => {
              // console.log(i,": ",info[i],typeof info[i])

              // identiftEle.selected = info[i]
              // identiftEle.disable = true
              // console.log(i," s et ")
              let identiftEle = this.$refs[i]
              // console.log(i)
              // console.log(identiftEle)
              identiftEle.selected = info[i]
              identiftEle.disable = true
            }, 500)
            continue
          }
          // console.log(i,": ",info[i],typeof info[i])
          if (this.$refs[i]) {
            // console.log(i,": ",info[i],typeof info[i])
            this.$refs[i].selected = info[i]
            // console.log('set diable on : ',i)
            this.$refs[i].disable = true
            // console.log(i," s et ")
          }
        }
      }
      // console.log(this.$refs.serviceProviderAddress.disableMark);
      // this.isLoading = true;
