<!-- 表单项中的下拉框选择 -->
<template>
  <div class="generalFormSelect">
    <div
      class="label"
      :style="{ width: selfLabelWidth, minWidth: selfLabelWidth }"
    >
      {{ labelTitle }}
    </div>
    <div class="selectZone">
      <el-select v-model="selected" :placeholder="'点击选择' + labelTitle">
        <el-option
          v-for="item in list"
          :key="item.id"
          :value="item.value"
          :label="item.label"
        />
      </el-select>
      <!-- <select
        v-model="selected"
        :style="{
          width: selfWidth,
          maxWidth: selfMaxWidth,
          minWidth: selfMinWidth,
        }"
      >
        <option value="" disabled>点击选择{{ labelTitle }}</option>
        <option v-for="item in list" :key="item.id" :value="item.value">
          {{ item.label }}
        </option>
      </select> -->
      <div class="select-arrow" style="margin-left: -20px; z-index: -1">
        <img src="../../../../../assets/image/common/down-12.svg" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "e-GeneralFormSelect",
  props: {
    // 标签text
    labelTitle: {
      type: String,
      default: "",
    },
    // 待选的options对象列表
    list: {
      type: Array,
      default() {
        return [];
      },
    },
    selfWidth: {
      type: String,
      default: "18%",
    },
    selfMaxWidth: {
      type: String,
      default: "200px",
    },
    selfMinWidth: {
      type: String,
      default: "150px",
    },
    selfLabelWidth: {
      type: String,
      default: "125px",
    },
  },
  data() {
    return {
      disable: false,
      selected: "",
      originalSelected: "",
      componentStatusCode: "100",
      targetData: null,
    };
  },
  watch: {
    selected(newVal) {
      // console.log('selected new val: ',newVal);
      this.$emit("selectValue", [this.labelTitle, newVal]);
      if (newVal !== "") {
        this.$emit("setLegitimacyTrue");
      }
      if (this.componentStatusCode === "100") {
        if (newVal !== "") {
          this.$emit("setNoEmptyTrue");
        }
      }
      if (this.componentStatusCode === "101") {
        if (newVal !== this.originalSelected) {
          this.$emit("setUpdateStatusTrue");
        } else {
          this.$emit("setUpdateStatusFalse");
        }
      }
      this.targetData = newVal;

      this.$emit("selectChanged", newVal);
      this.$bus.$emit("updateDataInCookie");
    },
  },
};
</script>

