<template>
    <div class="process-bar">
        <div @click="scrollTo(i)" v-for="(item ,i) in list" :key="i" class="moduleName" :class="state === i?'active':''">{{item}}</div>
    </div>
</template>

<script>
    export default {
        name: "SettingProcessBar",
        props: {
            // modules:{
            //     type:Array
            // }
        },
        mounted() {
            let e = document.getElementsByClassName("settingMoudle")
            for (let i = 0; i < e.length; i++) {
                this.offsets[i] = e[i].offsetTop
            }
            let updateState = this.updateState;
            this.internal = setInterval(function () {
                updateState()
            }, 100)
        },
        data() {
            return {
                list: [
                    "账户信息",
                    "认证详情",
                    "财税信息",
                    "授权信息",
                    "业务信息",
                ],
                state: 0,
                offsets: [],
                internal: 0,
            }
        },
        computed: {
        },
        methods: {
            updateState(){
                let e = document.getElementsByClassName("settingMoudle")
                for (let i = 0; i < e.length; i++) {
                    this.offsets[i] = e[i].offsetTop;
                }
                if(!document.getElementById("mainContent").scrollTop) return;
                let scrollNum = document.getElementById("mainContent").scrollTop+100;
                // console.log(scrollNum)
                for (let i = this.offsets.length; i > 0; i--) {
                    if (scrollNum >= this.offsets[i]) {
                        this.state = i;
                        return
                    }
                }
                this.state = 0;
            },
            scrollTo(val){
                    document.getElementById("mainContent").scrollTop=this.offsets[val]-80
                    this.updateState()
            }
        },
        beforeUnmount() {
            clearInterval(this.internal)
        }
    }
</script>

<style>
    .process-bar {
        width: 200px;
        position: sticky;
        top: 5px;
    }

    .moduleName {
        margin-top: 5px;
        height: 32px;
        padding-left: 14px;
        line-height: 32px;
        font-size: 16px;
        color: #8A8A8A;
        font-family: "PingFang SC";
        border-left: 1px solid gainsboro;
    }

    .active {
        border-left: 2px solid #93D900;
        color: #93D900;
    }
</style>
