<template>
  <div class="locationSelect">
    <div class="label" style="display: flex; align-items: center">
      {{ labelTitle }}
    </div>
    <div class="contentZone">
      <div class="selectSetZone">
        <div class="selectZone">
          <select
            :disabled="disableMark"
            v-model="levelOneSelectIndex"
            @change="levelOneSelected"
            :title="monitoredMixedObj.levelOneName"
          >
            <option value="" disabled>{{ defaultRegion }}</option>
            <option
              v-for="(item, index) in allAdministrativeDivisionsArray"
              :key="index"
              :value="index"
            >
              {{ item.Name }}
            </option>
          </select>
          <div
            class="select-arrow"
            style="
              display: flex;
              align-items: center;
              margin-left: -20px;
              z-index: -1;
            "
          >
            <img src="../../../../assets/image/common/down-12.svg" />
          </div>
        </div>
        <div class="selectZone" v-show="levelTwoArray.length !== 0">
          <select
            v-model="levelTwoSelectIndex"
            @change="levelTwoSelected"
            :title="monitoredMixedObj.levelTwoName"
          >
            <option value="" disabled>{{ levelTwoTitle }}</option>
            <option
              v-for="(item, index) in levelTwoArray"
              :key="index"
              :value="index"
            >
              {{ item.Name }}
            </option>
          </select>
          <div
            class=""
            style="
              display: flex;
              align-items: center;
              margin-left: -20px;
              z-index: -1;
            "
          >
            <img src="../../../../assets/image/common/down-12.svg" />
          </div>
        </div>
        <div class="selectZone" v-show="levelThreeArray.length !== 0">
          <select
            v-model="levelThreeSelectIndex"
            @change="levelThreeSelected"
            :title="monitoredMixedObj.levelThreeName"
          >
            <option value="" disabled>城市/区/郡</option>
            <option
              v-for="(item, index) in levelThreeArray"
              :key="index"
              :value="index"
            >
              {{ item.Name }}
            </option>
          </select>
          <div style="margin-left: -20px; z-index: -1">
            <img src="../../../../assets/image/common/down-12.svg" />
          </div>
        </div>
        <div class="selectZone" v-show="levelFourArray.length !== 0">
          <select
            v-model="levelFourSelectIndex"
            @change="levelFourSelected"
            :title="monitoredMixedObj.levelFourName"
          >
            <option value="" disabled>区/县</option>
            <option
              v-for="(item, index) in levelFourArray"
              :key="index"
              :value="index"
            >
              {{ item.Name }}
            </option>
          </select>
          <div style="margin-left: -20px; z-index: -1">
            <img src="../../../../assets/image/common/down-12.svg" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import globalCityData from "../../../../assets/json/global.json";
export default {
  name: "GlobalCitySelect",
  async created() {
    this.allAdministrativeDivisionsArray = window.JSON.parse(
      window.JSON.stringify(globalCityData.Location.CountryRegion)
    );
  },
  props: {
    // 标签text
    labelTitle: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      disableMark: false,
      defaultRegion: "国家/地区",
      allAdministrativeDivisionsArray: [],
      levelOneSelectIndex: "",
      levelTwoArray: [],
      levelTwoSelectIndex: "",
      levelThreeArray: [],
      levelThreeSelectIndex: "",
      levelFourArray: [],
      levelFourSelectIndex: "",
      componentStatusCode: "100",
      targetData: null,

      levelTwoTitle: "省/自治区/直辖市",

      monitoredMixedObj: {
        levelOneName: "",
        levelTwoName: "",
        levelThreeName: "",
        levelFourName: "",
      },

      originalMixedObj: {
        levelOneName: "",
        levelTwoName: "",
        levelThreeName: "",
        levelFourName: "",
      },
    };
  },
  methods: {
    levelOneSelected() {
      console.log("国家以及选项");
      const lv1Index = Number(this.levelOneSelectIndex);
      this.$set(
        this.monitoredMixedObj,
        "levelOneName",
        this.allAdministrativeDivisionsArray[lv1Index].Name
      );
      // console.log(this.allAdministrativeDivisionsArray[lv1Index].Name);
      this.$emit(
        "countrySelected",
        this.allAdministrativeDivisionsArray[lv1Index].Name
      );
      this.levelTwoArray = [];
      this.levelTwoSelectIndex = "";
      this.$set(this.monitoredMixedObj, "levelTwoName", "");
      this.levelThreeArray = [];
      this.levelThreeSelectIndex = "";
      this.$set(this.monitoredMixedObj, "levelThreeName", "");
      this.levelFourArray = [];
      this.levelFourSelectIndex = "";
      this.$set(this.monitoredMixedObj, "levelFourName", "");

      if (
        this.allAdministrativeDivisionsArray[lv1Index].hasOwnProperty("State")
      ) {
        const objType = Object.prototype.toString.apply(
          this.allAdministrativeDivisionsArray[lv1Index].State
        );
        if (objType === "[object Object]") {
          if (
            this.allAdministrativeDivisionsArray[lv1Index].State.hasOwnProperty(
              "City"
            )
          ) {
            this.levelThreeArray = window.JSON.parse(
              window.JSON.stringify(
                this.allAdministrativeDivisionsArray[lv1Index].State.City
              )
            );
          }
        }
        if (objType === "[object Array]") {
          this.levelTwoArray = window.JSON.parse(
            window.JSON.stringify(
              this.allAdministrativeDivisionsArray[lv1Index].State
            )
          );
          //一共6个国家有二级的省/州行政区划0(中国),18(澳大利亚),82(韩国),124(马来西亚),133(美国),230(英国)
          switch (this.levelOneSelectIndex) {
            case 0:
              this.levelTwoTitle = "省/自治区/直辖市";
              break;
            case 18:
              this.levelTwoTitle = "州/领地";
              break;
            case 82:
              this.levelTwoTitle = "道/广域市";
              break;
            case 124:
              this.levelTwoTitle = "州/联邦直辖区";
              break;
            case 133:
              this.levelTwoTitle = "州";
              break;
            case 230:
              this.levelTwoTitle = "区域";
              break;
            default:
              this.levelTwoTitle = "默认";
          }
        }
      }
    },
    levelTwoSelected() {
      const lv2Index = Number(this.levelTwoSelectIndex);
      this.$set(
        this.monitoredMixedObj,
        "levelTwoName",
        this.levelTwoArray[lv2Index].Name
      );
      // console.log(this.levelTwoArray[lv2Index].Name);
      this.$emit("provinceSelected", this.levelTwoArray[lv2Index].Name);
      this.levelThreeArray = [];
      this.levelThreeSelectIndex = "";
      this.$set(this.monitoredMixedObj, "levelThreeName", "");
      this.levelFourArray = [];
      this.levelFourSelectIndex = "";
      this.$set(this.monitoredMixedObj, "levelFourName", "");

      if (this.levelTwoArray[lv2Index].hasOwnProperty("City")) {
        const objType = Object.prototype.toString.apply(
          this.levelTwoArray[lv2Index].City
        );
        if (objType === "[object Array]") {
          this.levelThreeArray = window.JSON.parse(
            window.JSON.stringify(this.levelTwoArray[lv2Index].City)
          );
        }
        if (objType === "[object Object]") {
          this.levelThreeArray.splice(0, this.levelThreeArray.length);
          this.levelThreeArray.push(this.levelTwoArray[lv2Index].City);
        }
      }
    },
    levelThreeSelected() {
      const lv3Index = Number(this.levelThreeSelectIndex);
      this.$set(
        this.monitoredMixedObj,
        "levelThreeName",
        this.levelThreeArray[lv3Index].Name
      );
      // console.log(this.levelThreeArray[lv3Index].Name);
      this.$emit("citySelected", this.levelThreeArray[lv3Index].Name);
      this.levelFourArray = [];
      this.levelFourSelectIndex = "";
      this.$set(this.monitoredMixedObj, "levelFourName", "");

      if (this.levelThreeArray[lv3Index].hasOwnProperty("Region")) {
        const objType = Object.prototype.toString.apply(
          this.levelThreeArray[lv3Index].Region
        );
        if (objType === "[object Array]") {
          this.levelFourArray = window.JSON.parse(
            window.JSON.stringify(this.levelThreeArray[lv3Index].Region)
          );
        }
        if (objType === "[object Object]") {
          this.levelFourArray.splice(0, this.levelFourArray.length);
          this.levelFourArray.push(this.levelThreeArray[lv3Index].Region);
        }
      }
    },
    levelFourSelected() {
      const lv4Index = Number(this.levelFourSelectIndex);
      this.$set(
        this.monitoredMixedObj,
        "levelFourName",
        this.levelFourArray[lv4Index].Name
      );
      // console.log(this.levelFourArray[lv4Index].Name);
      this.$emit("districtSelected", this.levelFourArray[lv4Index].Name);
    },
  },
  watch: {
    monitoredMixedObj: {
      handler(newVal) {
        if (
          newVal.levelOneName !== "" &&
          newVal.levelTwoName !== "" &&
          newVal.levelThreeName !== "" &&
          newVal.levelFourName !== ""
        ) {
          this.$emit("setLegitimacyTrue");
        } else {
          this.$emit("setLegitimacyFalse");
        }

        if (
          newVal.levelOneName !== this.originalMixedObj.levelOneName ||
          newVal.levelTwoName !== this.originalMixedObj.levelTwoName ||
          newVal.levelThreeName !== this.originalMixedObj.levelThreeName ||
          newVal.levelFourName !== this.originalMixedObj.levelFourName
        ) {
          this.$emit("setUpdateStatusTrue");
        } else {
          this.$emit("setUpdateStatusFalse");
        }

        const copy = window.JSON.parse(window.JSON.stringify(newVal));
        this.targetData = copy;
      },
      deep: true,
    },
  },
};
</script>

